.collection .collection-item {
    background-color: #fff;
    line-height: 1.2em;
    padding: 10px 20px;
    margin: 0;
    border: 1px solid #dddbd9;
}
.collection a.collection-item {
    display: block;
    -webkit-transition: .25s;
    -o-transition: .25s;
    transition: .25s;
    color: rgb(239, 147, 151);
    font-size: 1.4em;

}


